import * as THREE from "three";

const img1: string = `data:image/png;base64,`;

const img2: string = `data:image/png;base64,`;

const img3: string = `data:image/png;base64,`;

const img4: string = `data:image/png;base64,`;

const loader = new THREE.TextureLoader();

export const texture1: THREE.Texture = loader.load(img1);

export const texture2: THREE.Texture = loader.load(img2);

export const texture3: THREE.Texture = loader.load(img3);

export const texture4: THREE.Texture = loader.load(img4);

export default {
  texture1,
  texture2,
  texture3,
  texture4,
};
